import { Component, OnInit, AfterViewInit } from '@angular/core';
import * as $ from 'jquery';
import { Router, NavigationEnd } from '@angular/router';
import { StringConst } from 'src/app/@core/utils/string-constants';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, AfterViewInit {
    public pushRightClass: string;
    public doctor = false;
    public pharmacy = false;
    constructor(public router: Router) {

        this.router.events.subscribe(val => {
            if (
                val instanceof NavigationEnd &&
                window.innerWidth <= 992 &&
                this.isToggled()
            ) {
                this.toggleSidebar();
            }
        });
    }

    logoutProvider() {
     
    }

    ngOnInit() {
        this.pushRightClass = 'push-right';
    
    }

    isToggled(): boolean {
        const dom: Element = document.querySelector('body');
        return dom.classList.contains(this.pushRightClass);
    }

    toggleSidebar() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle(this.pushRightClass);
    }

    rltAndLtr() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('rtl');
    }

    ngAfterViewInit() {
        $('.toggle-button').click(function () {
            $('.sidebar').toggleClass('custom-sidebar');
            $('.main-container').toggleClass('custom-main-container');
            $('.admin-header').toggleClass('custom-admin-header');
        });
        $(window).resize(function () {
            const windowWidth = $(window).width();
        if (windowWidth < 993) {
            // console.log("widnow is resizing and current window width is " + windowWidth);
            $('.sidebar').toggleClass('custom-sidebar');
            $('.main-container').toggleClass('custom-main-container');
            $('.admin-header').toggleClass('custom-admin-header');
        } else {
            // console.log('no match');
            $('.sidebar').removeClass('custom-sidebar');
            $('.main-container').removeClass('custom-main-container');
            $('.admin-header').removeClass('custom-admin-header');
        }
        });

        $(window).on('load', function () {
            const windowWidth = $(window).width();
        if (windowWidth < 993) {
            $('.sidebar').toggleClass('custom-sidebar');
            $('.main-container').toggleClass('custom-main-container');
            $('.admin-header').toggleClass('custom-admin-header');
        } else {
            // console.log('no match');
            $('.sidebar').removeClass('custom-sidebar');
            $('.main-container').removeClass('custom-main-container');
            $('.admin-header').removeClass('custom-admin-header');
        }
        });
    }
}
