import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';
import * as _ from 'underscore';
import { HelperService } from 'src/app/services/helper.service';
import { DoctorSidebarComponent } from '../doctor-sidebar/doctor-sidebar.component';
import { PharmacySidebarComponent } from '../pharmacy-sidebar/pharmacy-sidebar.component';
import { PatientSidebarComponent } from '../patient-sidebar/patient-sidebar.component';
import { SharedData } from 'src/app/@core/services/shared.config';
import { HospitalSidebarComponent } from '../hospital-sidebar/hospital-sidebar.component';
import { NgoSidebarComponent } from '../ngo-sidebar/ngo-sidebar.component';

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
    public roleType: any;
    public componentRef: any;
    public isLoading = false;
    private config = new SharedData();
    @ViewChild('sidebarcontainer', {static: true, read: ViewContainerRef }) entry: ViewContainerRef;
    constructor(private helperService: HelperService, private resolver: ComponentFactoryResolver) {}
    ngOnInit() {
      this.helperService.getUserPermission().subscribe(response => {
        this.isLoading = true;
        this.roleType = response['data'].role_type ? response['data'].role_type : '' ;
        this.createComponent(this.config.decriptData(this.roleType));
      });
    }
    createComponent(role_type) {
      if (role_type === 'doctor') {
        this.entry.clear();
        const factory = this.resolver.resolveComponentFactory(DoctorSidebarComponent);
        this.componentRef = this.entry.createComponent(factory);
        this.isLoading = false;
      } else if (role_type === 'pharmacy') {
        this.entry.clear();
        const factory = this.resolver.resolveComponentFactory(PharmacySidebarComponent);
        this.componentRef = this.entry.createComponent(factory);
        this.isLoading = false;
      } else if (role_type === 'patient') {
        this.entry.clear();
        const factory = this.resolver.resolveComponentFactory(PatientSidebarComponent);
        this.componentRef = this.entry.createComponent(factory);
        this.isLoading = false;
      } else if (role_type === 'NGO') {
        this.entry.clear();
        const factory = this.resolver.resolveComponentFactory(NgoSidebarComponent);
        this.componentRef = this.entry.createComponent(factory);
        this.isLoading = false;
      } else if (role_type === 'Hospital') {
        this.entry.clear();
        const factory = this.resolver.resolveComponentFactory(HospitalSidebarComponent);
        this.componentRef = this.entry.createComponent(factory);
        this.isLoading = false;
      }
    }
  }
