import { Component, OnInit, AfterContentChecked, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-hospital-sidebar',
  templateUrl: './hospital-sidebar.component.html',
  styleUrls: ['./hospital-sidebar.component.scss']
})
export class HospitalSidebarComponent implements OnInit, AfterContentChecked {

  isActive: boolean;
  collapsed: boolean;
  showMenu: string;
  pushRightClass: string;
  menus: any;
  sidebarhide: Boolean = false;
  showSettingMenu: string;
  showOrderMenu: string;

  @Output() collapsedEvent = new EventEmitter<boolean>();

  constructor(public router: Router, private changeDetector: ChangeDetectorRef) {
    this.router.events.subscribe(val => {
        if (
            val instanceof NavigationEnd &&
            window.innerWidth <= 992 &&
            this.isToggled()
        ) {
            this.toggleSidebar();
        }
    });
  }
  ngAfterContentChecked() {
    this.changeDetector.detectChanges();
  }

  SettingMenu(element: any) {
    if (element === this.showSettingMenu){
      this.showSettingMenu = '0';
    } else {
      this.showSettingMenu = element;
    }
  }

  ngOnInit() {
      this.isActive = false;
      this.collapsed = false;
      this.showMenu = '';
      this.pushRightClass = 'push-right';
  }


  eventCalled() {
      this.isActive = !this.isActive;
  }

  addExpandClass(element: any) {
      if (element === this.showMenu) {
          this.showMenu = '0';
      } else {
          this.showMenu = element;
      }
  }

  toggleCollapsed() {
      this.collapsed = !this.collapsed;
      this.collapsedEvent.emit(this.collapsed);
  }

  isToggled(): boolean {
      const dom: Element = document.querySelector('body');
      return dom.classList.contains(this.pushRightClass);
  }

  toggleSidebar() {
      const dom: any = document.querySelector('body');
      dom.classList.toggle(this.pushRightClass);
  }

  rltAndLtr() {
      const dom: any = document.querySelector('body');
      dom.classList.toggle('rtl');
  }

  // changeLang(language: string) {
  //     this.translate.use(language);
  // }

  onLoggedout() {
      localStorage.removeItem('isLoggedin');
  }
}
